<template>
    <v-container fluid class="qiong-txt-td">

        <qiong-header-func class="qiong-pl-25 qiong-pr-25">
            <qiong-title slot="title">
                獎金報表
            </qiong-title>

            <div slot="things" class="">
                <div>
                    {{ view.getTodayEn() }}
                </div>
                <div class="mt-1" v-if="$store.state.bonus.chronu">
                    獎金周期&nbsp;:&nbsp;由{{ view.ser_timed($store.state.bonus.chronu.start, true) }}至&nbsp;{{ view.ser_timed($store.state.bonus.chronu.end, true) }}
                </div>
                <div v-else>&nbsp;</div>
            </div>
        </qiong-header-func>

        <as-top-msg :me="user_back" class="qiong-pl-25 qiong-pr-25"
            v-if="user_back"
            @buyed_Father="buyedLocation"
        ></as-top-msg>

        <alert-buy-none class="qiong-pl-25 qiong-pr-25"
            :_buyed="buyed"
            :_bonused="bonused">
        </alert-buy-none>

        <v-row class="qiong-pt-15 mt-6">
            <as-recommend class="qiong-pager-zebra qiong-pl-25 qiong-pr-25" :me="mine" :childs="childrens" v-if="childrens"></as-recommend>
            <qiong-loading v-else></qiong-loading>

            <as-active-house class="qiong-pl-25 qiong-pr-25" :me="mine" :childs="childrens" v-if="childrens"></as-active-house>
            <qiong-loading v-else></qiong-loading>

            <as-team-layer class="qiong-pager-zebra qiong-pl-25 qiong-pr-25" :me="mine" :childs="childrens" v-if="childrens"></as-team-layer>
            <qiong-loading v-else></qiong-loading>

            <!-- as-can-num-son class="qiong-pl-25 qiong-pr-25" :me="mine" :childs="childrens" v-if="childrens"></as-can-num-son -->
        
            <qiong-space :space="'5vh'" class="qiong-pager-zebra"></qiong-space>

        </v-row>

        <v-row class="qiong-pager-zebra">
            <v-col>
                <qiong-go-top></qiong-go-top>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import QiongGoTop from '../../../components/Qiong/Button/QiongGoTop.vue'
import QiongHeaderFunc from '../../../components/Qiong/Pager/QiongHeaderFunc.vue'
import QiongEmpty from '../../../components/Qiong/Ui/QiongEmpty.vue'
import QiongLoading from '../../../components/Qiong/Ui/QiongLoading.vue'
import QiongSpace from '../../../components/Qiong/Ui/QiongSpace.vue'
import QiongTitle from '../../../components/Qiong/Ui/QiongTitle.vue'
import AlertBuyNone from '../../../components/Viewing/Alert/AlertBuyNone.vue'
import AsActiveHouse from './Bonus/AsActiveHouse.vue'
import AsCanNumSon from './Bonus/AsCanNumSon.vue'
import AsRecommend from './Bonus/AsRecommend.vue'
import AsTeamLayer from './Bonus/AsTeamLayer.vue'
import AsDialog from './Inner/AsDialog.vue'
import AsTopMsg from './Inner/AsTopMsg.vue'
    export default {
  components: { QiongHeaderFunc, QiongTitle, AsTopMsg,
            AsRecommend,
            AsActiveHouse,
            AsTeamLayer,
            AsCanNumSon,
            QiongSpace,
            QiongLoading,
            QiongEmpty,
            AsDialog,
    AlertBuyNone,
    QiongGoTop      
        },
        name: '',
        props: {
            user_Sending: {
                type: Object,
                default: null
            }
        },
        data() {
            return {
                mine: null,
                childrens: null,

                user_back: null,

                buyed: true,
                bonused: true,

                loading: true
            }
        },
        watch: {
            
        },
        async mounted() {
            if (this.user_Sending != null) {
                this.user_back = this.user_Sending
            } else {
                this.user_back = this.$store.state.user_backend
            }

            console.log('STATE' ,this.user_Sending, this.user_back, this.$store.state.token)
            await this.buyLoding()
        },
        methods: {
            async buyLoding() {

                const res = await this.conn.get(
                    this.api.bonus,
                    this.$store.state.token,
                    {
                        'chronu': this.$store.state.chronus.id,
                        'user': this.user_back.id
                    }
                )

                if (res) {
                    if (res.length > 0) {
                        this.$store.commit('saveBonus', res[0])
                    } else {
                        this.bonused = false
                    }
                }

                await this.lockMine()
            },

            lockMe(user, transfer) {
                if (user.unid == this.user_back.unid) {

                    let res = JSON.parse(JSON.stringify(user))
                    this.mine = res
                }
            },
            lockChilds(user, transfer) {
                if (user.unid != this.mine.unid) {
                    this.childrens.push(user)
                }
            },
            async lockMine() {

                let many = this.$store.state.users_sponser
                const transfer = this.$store.state.transfer
                
                if (many) {
                    this.bonus._build_res(many, transfer, this.lockMe)
                }

                const usd_tl = await this.bonus.team_layer( [ this.mine ], transfer)
                const usd_re = await this.bonus.recommend( [ this.mine ], transfer )

                this.loading = false

                if (this.mine) {
                    this.childrens = [ ]
                    this.bonus._build_res([ this.mine ], transfer, this.lockChilds)

                    this.loading = true
                }
            },
            back() {
                this.$router.push({
                    path: '/home/account' })
            },

            buyedLocation(v) {
                this.buyed = v
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


